@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.audit-trail-wrapper {
  .trail-container {
    position: relative;

    &:not(:last-child)::before {
      content: '';
      background-color: pick-color('primary', 'web');
      height: 100%;
      width: 4px;
      display: block;
      position: absolute;
      top: 10px;
      left: 14px;
      transform: translate(-50%);
    }

    .trail-step {
      @include circles(28px, pick-color('primary', 'web'));
      flex-shrink: 0;
    }

    .trail {
      position: relative;

      .trail-user {
        margin-bottom: 12px;

        mat-icon {
          width: 28px;
          height: 28px;
          font-size: 28px;
        }
      }

      .trail-content {
        margin-bottom: 6px;
        word-break: break-all;
      }
    }
  }
}
